body{
  background-color: #000;
  color: white;
}
.App {
  text-align: center;
}
.busca-caracores-button-reset{
  margin:'10px';
  padding:'10px';
  width: '80px';
  height:'80px';
  background-color: 'green';
  color: 'white';
  box-shadow: 'none';
  border:'0';
  font-size:'15px';
  font-weight:'800';
  border-radius: '50%';
  }
  .busca-caracores-button-reset:hover{
      background-color: 'white';
  color: 'green';
  }
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
